.main-calendar-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px;

  .header {
    display: flex;
    margin: 20px 0;
    flex-shrink: 0;

    .current-date {
      flex: 1;
      font-size: 20px;
      font-weight: bold;
      text-align: center;
    }
  }

  #calendar-view {
    display: flex;
    flex-direction: column;
    font-size: 14px;

    .fc-view-container {
      overflow: auto;
      overflow: overlay;
      margin-right: -10px;
      padding-right: 10px;
    }

    .fc-toolbar {
      display: none;
    }
  }
}

#calendar-list-view {
  display: flex;
  flex-direction: column;
  //padding: 20px;

  .fc-toolbar,
  .fc-list-heading {
    display: none;
  }

  * {
    border: none;
  }
}

.fc-scroller {
  height: auto !important;
}
