.colored-dot {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
  position: relative;
  top: -1px;
  margin-right: 5px;
  background: $blue;

  &.red {
    background: $red;
  }

  &.yellow {
    background: $yellow;
  }

  &.purple {
    background: $purple;
  }

  &.green {
    background: $green;
  }
}
