.right-aside {
  background: $gray-200;
  box-shadow: -1px 0 rgba(0, 0, 0, 0.1);
  width: 300px;
  flex: 0 0 300px;
  display: flex;
  flex-direction: column;

  .current-date {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
  }

  .DayPicker {
    padding: 20px;
    box-shadow: 0 1px rgba(0, 0, 0, 0.1);

    * {
      outline: none;
    }

    .DayPicker-TodayButton {
      background: $blue;
      color: white;
      padding: 5px;
      border-radius: 5px;
      display: block;
      margin: auto;
    }
  }

  #calendar-list-view {
    padding: 20px;
  }
}
