@import "roboto-font";

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
html {
  //height: 100%;
}

html {
  min-width: 800px;
  font-size: 14px;
}

body {
  color: $gray-1000;
  font-size: 12px;
  background: white;
  background-size: cover, auto;
  line-height: 1.15;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;

  //&:link,
  //&:visited {
  //  color: $blue-500;
  //}
  //
  //&:hover {
  //  color: $blue-600;
  //}

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    color: $gray-1000 !important;
  }
}

ul {
  margin: 0;
  padding: 0;
}

.hidden {
  display: none !important;
}

#root {
  height: 100vh;
}

::-ms-clear {
  display: none;
}

::-ms-reveal {
  display: none;
}

::-webkit-scrollbar {
  width: 0;
  height: 10px;
  background: transparent;
  transition: width 0.2s ease-in-out;
  will-change: width;
}

::-webkit-scrollbar * {
  background: transparent;
}

::-webkit-scrollbar-button {
  display: none;
  width: 0;
  height: 0;
}

::-webkit-scrollbar-thumb {
  border: 2px solid transparent;
  border-radius: 10px;
  box-shadow: inset 0 0 0 8px rgba(0, 0, 0, 0.5);
  transition: box-shadow 0.2s ease-in-out;
}

::-webkit-scrollbar-track {
  background: transparent;
}

:hover::-webkit-scrollbar,
body::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb:hover {
  box-shadow: inset 0 0 0 8px rgba(0, 0, 0, 0.7);
}
