.mac-btn {
  text-decoration: none;
  font-family: ".LucidaGrandeUI", "Lucida Grande", "Lucida sans unicode";
  color: black;
  font-size: 13px;
  padding: 4px 11px;
  border: 1px solid #9c9c9c;
  margin: 2px;
  display: inline-block;
  background-image: -webkit-linear-gradient(#fff, #f6f6f6 30%, #f3f3f3 45%, #ededed 60%, #eee);
  background-image: linear-gradient(#fff, #f6f6f6 30%, #f3f3f3 45%, #ededed 60%, #eee);
  border-radius: 3px;
  cursor: default;
  box-shadow: 0 0 1px rgba(0, 0, 0, .2);
  outline: none;
  text-transform: capitalize;

  &:active,
  &.active {
    border-color: #705ebb;
    background-image: linear-gradient(
                    #acc5e9 0%, #a3c0f2 18%,
                    #61a0ed 39%, #55a3f2 70%,
                    #82c2f1 91.72%, #9AD2F2 100%
    );
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.65);
  }
}

.btn-group {
  .btn:not(:first-child) {
    margin-left: 0;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  .btn:not(:last-child) {
    margin-right: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
}
