.sidebar {
  display: flex;
  flex-direction: column;
  width: 250px;
  flex: 0 0 250px;
  background: $gray-100;
  box-shadow: 1px 0 rgba(0, 0, 0, 0.1);

  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 40px;
    box-shadow: 0 1px rgba(0, 0, 0, 0.1);

    .avatar {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      margin-bottom: 10px;
      box-shadow: 1px 1px 0 1px #00000029;
    }

    .fullname {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 10px;
    }

    .badge {
      text-transform: uppercase;
      font-size: 10px;
      color: white;
      padding: 5px;
      background: $blue;
      border-radius: 5px;
    }
  }

  .main {
    flex: 1;
    padding: 20px;
    box-shadow: 0 1px rgba(0, 0, 0, 0.1);

    .title {
      color: $gray-800;
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: 5px;
    }
  }

  .footer {
    padding: 20px;
  }

  .menu {

    .menu-item {
      display: flex;
      align-items: center;
      padding: 5px;
      cursor: pointer;
      color: $gray-700;

      .menu-icon {
        width: 15px;
        height: 15px;
        margin-right: 10px;
        fill: currentColor;
      }

      .menu-title {
        font-weight: 500;
        text-transform: uppercase;

      }

      &.active,
      &:hover {
        color: $gray-1000;
      }

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}
